.Radio input {
  display: none;
}

.Radio label {
  display: flex;
  min-width: 100px;
  box-shadow: 0 0 0 1px #a8c8ce;
  align-items: center;
  justify-content: center;
  background: #f1f7f8;
  border-radius: 1px;
  height: 23px;
  color: #195e79;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.Radio input:checked + label {
  position: relative;
  background: #55bcca;
  color: #fafaf5;
  z-index: 1;
  box-shadow: 0 0 0 1px teal;
}
